.preview-product {
  display: inline-block;

  width: 100%;
  margin-bottom: 1rem;

  background: white;

  .preview-product-container {
    display: flex;

    width: 100%;
    height: 100%;
    padding: 1rem;
    border: none;

    .preview-product-cover {
      position: relative;
      flex: none;
      width: 33.33%;

      img {
        width: 100%;
        height: auto;
      }
    }

    .preview-product-body {
      flex: 1;
      display: flex;
      flex-direction: column;

      padding: 1rem 2rem;

      color: color(text);
      font-weight: 700;
      font-size: 1.6rem;
      line-height: 1.2em;

      header.preview-product-header {
        .preview-product-title {
          text-transform: uppercase;
          font-size: 2.8rem;
          line-height: 1.2em;
          margin-bottom: 1rem;
        }

        .preview-product-meta {
          position: relative;
          margin-bottom: 0.4em;

          &[data-label]::before {
            content: attr(data-label);
            font-weight: bold;
            color: color(gray);
            text-transform: lowercase;
            font-variant: small-caps;
          }
        }
      }

      .preview-product-content {
        flex: 1;
        margin-top: 1rem;
        font-weight: normal;
        line-height: 1.4em;
      }
    }
  }
}


@media (max-width: breakpoint(phone)) {
  .preview-product {
    .preview-product-container {
      flex-direction: column;

      .preview-product-cover {
        border: none;
        width: 100%;
        margin-bottom: 1rem;
        &::before { display: none; }
        .preview-product-cover-content {
          position: relative;
          text-align: left;
          transform: none;
          line-height: 1.2em;
          margin-bottom: 1em;

          .preview-product-date {
            font-size: 2rem;
            font-weight: bold;
          }

          .preview-product-tag {
            font-size: 1.4rem;
            font-weight: bold;
            text-transform: uppercase;
          }
        }
      }

      .preview-product-body{
        padding: 0;
      }
    }
  }
}



